namespace $ {
	export class $hyoo_studio_value_number extends $hyoo_studio_value_unit {
		
		/**
		 * ```tree
		 * self / <= Numb
		 * ```
		 */
		self() {
			return [
				this.Numb()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * numb? +NaN
		 * ```
		 */
		@ $mol_mem
		numb(next?: any) {
			if ( next !== undefined ) return next as never
			return +NaN
		}
		
		/**
		 * ```tree
		 * Numb $mol_number value? <=> numb?
		 * ```
		 */
		@ $mol_mem
		Numb() {
			const obj = new this.$.$mol_number()
			
			obj.value = (next?: any) => this.numb(next)
			
			return obj
		}
	}
	
}

