			obj.hint = () => this.$.$mol_locale.text( '$hyoo_studio_value_object_Over_prop_add_hint' )
			obj.align_hor = () => "left"
			obj.Trigger_icon = () => this.Over_add_icon()
			obj.value = (next?: any) => this.over_prop_add(next)
